import { Component, OnInit } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { User } from '../models/user.model';
import { UserService } from '../services/user.service';
import { Router } from '@angular/router';
import { AuthenticationService } from '../services/authentication.service';

@Component({
  selector: 'app-userprofile',
  templateUrl: './userprofile.page.html',
  styleUrls: ['./userprofile.page.scss'],
})
export class UserprofilePage implements OnInit {

  name:any;
  user:any;
  private currentUserSubject: BehaviorSubject<User>;
  public currentUser: Observable<User>;
  regno:any;

  constructor(private userservice:UserService,private router:Router,private authService:AuthenticationService) { }

  ngOnInit() {
    this.currentUserSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('currentUser')));
    this.currentUser = this.currentUserSubject.asObservable();
    this.regno=this.currentUserSubject.value['data'].regno;
    console.log(this.regno);

    this.userservice.getUser(this.regno).subscribe(data=>{
      if(data['status']==200){
        this.user=data['data'];
        this.name=this.user.name;
        console.log(this.user.name);
      }
      else{
        console.log(data);
      }  
    })
  }


  logOut(){
    this.authService.logout();
    this.router.navigate(['/login']);
  }

}
